import { Component, Input, OnInit } from "@angular/core";
import { ModalService } from "../../../service/modal/modal.service";
import { Router } from "@angular/router";
import { WindowActionService } from "../../../service/window-action.service";
import { AuthData } from "../../../data/auth.data";
import {ListWarehouseDto, ResponseListWarehouseDto, Warehouse, WarehouseService} from "@youtap-web-lib";

@Component({
  selector: "supplier-section-list",
  templateUrl: "./supplier-section-list.component.html"
})
export class SupplierSectionListComponent implements OnInit{
  loadingWarehouse = false;
  isGetWarehouse = true;
  dataWarehouse!: ResponseListWarehouseDto;
  warehouses: Warehouse[] = [];
  showWarehouse: boolean[] = [];
  token = "";
  @Input() supplierIgnore = [""];

  @Input() set page(data: number) {
    if (data && this.isGetWarehouse) {
      this.getListWarehouse(data);
    }
  }

  constructor(
    private modalService: ModalService,
    private router: Router,
    private windowActionService: WindowActionService,
    private authData: AuthData,
    private warehouseService: WarehouseService
  ) {
  }

  ngOnInit() {
    this.token = this.authData.token;
  }

  getListWarehouse(page: number) {
    this.loadingWarehouse = true;
    const data: ListWarehouseDto = {
      listWarehouseInput: {
        page: page,
        limit: 5,
        filter: {
          ignoreWarehouseIds: this.supplierIgnore,
        }
      }
    };

    this.warehouseService.listWarehouse(data).subscribe({
      next:({data}) => {
        this.dataWarehouse = data;
        if (data.listWarehouse.warehouses.length) {
          data.listWarehouse.warehouses.forEach((warehouse) => {
            this.warehouses.push(warehouse);
            this.showWarehouse.push(true);
          })
        }
        if (!data.listWarehouse.warehouses.length) {
          this.isGetWarehouse = false;
        }
        this.loadingWarehouse = false;
      },
      error: () => {
            this.loadingWarehouse = false;
      }
    })
  }

  checkLength(haveData: boolean, index: number) {
    this.showWarehouse[index] = haveData;
  }

  setLocation() {
    if (this.token) {
      this.router.navigate(["/address/list"]);
    } else {
      this.router.navigate(["/location/set"], {});
    }
  }
}
