import { Component, Input, OnInit } from '@angular/core';
import { Router } from "@angular/router";
import * as isToday from 'dayjs/plugin/isToday';
import * as dayjs from 'dayjs';

dayjs.extend(isToday);

import { ListBannerConfigurationInputDto } from '@youtap-web-lib';

import { GaService } from "../../../service/ga.service";
import { WindowActionService } from "../../../service/window-action.service";
import { BannerListSignalService } from '../../../service/signal/banner-list.signal.service';
import { EventData } from '../../../data/event-data';
import { AuthData } from '../../../data/auth.data';

@Component({
  selector: 'popup-banner',
  templateUrl: './popup-banner.component.html',
  styleUrl: './popup-banner.component.scss'
})
export class PopupBannerComponent implements OnInit {
  @Input() LocationBanner = '';
  @Input() TypeBanner = '';
  @Input() PlatformBanner = '';

  dataInputVariable!: ListBannerConfigurationInputDto;
  showPopup = false;

  constructor(
    private router: Router,
    private authData: AuthData,
    private gaService: GaService,
    private windowActionService: WindowActionService,
    public bannerListSignalService: BannerListSignalService,
    private eventData: EventData
  ) {
  }

  ngOnInit(): void {
    this.getPopBannerData();
  }

  getPopBannerData() {
    this.dataInputVariable = {
      listBannerConfigurationInput: {
        location: this.LocationBanner,
        type: this.TypeBanner,
        platform: this.PlatformBanner,
      }
    }
    this.bannerListSignalService.get(this.dataInputVariable).then(() => {
      if (this.bannerListSignalService.bannerPopupShow()) {
        setTimeout(() => {
          this.showPopup = true
        }, 2000)
      }
    });
  }

  closePopup() {
    this.gaService.addEvent('banner_popup_close', 'banner_popup', "")
    this.showPopup = false;
    // this.bannerListSignalService.closeBanner(this.LocationBanner);
  }

  openBanner(url?: string, name?: string, type?: string) {
    this.gaService.addEvent('banner_popup_area_' + (name ? name : ''), 'banner_popup', "")
    if (type === "EXTERNAL") {
      this.windowActionService.open(url ? url : '', '_blank')
    } else {
      this.router.navigateByUrl(url ? url : '')
    }
  }


}
