import {
  Inject,
  Injectable,
  PLATFORM_ID,
  computed,
  signal
} from '@angular/core';
import {
  BannerService,
  ResponseListBannerConfigurationDto,
  ListBannerConfigurationInputDto,
  TypeListBanner
} from '@youtap-web-lib';
import { Subject, takeUntil } from 'rxjs';
import { TransferStateService } from '../transfer-state.service';

const BannerSliderConfigurationInitialState = {
  dataSlider$: {} as ResponseListBannerConfigurationDto,
  loadedSlider$: false,
  loadingSlider$: false,
  errorSlider$: null
};

const BannerPopUpConfigurationInitialState = {
  dataPopUp$: {} as ResponseListBannerConfigurationDto,
  loadedPopUp$: false,
  loadingPopUp$: false,
  errorPopUp$: null
};

const STATE_BANNER_SLIDER_KEY = 'banner-slider';
const STATE_BANNER_POPUP_KEY = 'banner-popup';

@Injectable({ providedIn: 'root' })
export class BannerListSignalService {
  private terminate$ = new Subject<void>();

  stateSlider = signal(BannerSliderConfigurationInitialState);
  dataSlider$ = computed(() => this.stateSlider().dataSlider$);
  loadedSlider$ = computed(() => this.stateSlider().loadedSlider$);
  loadingSlider$ = computed(() => this.stateSlider().loadingSlider$);
  errorSlider$ = computed(() => this.stateSlider().errorSlider$);

  statePopUp = signal(BannerPopUpConfigurationInitialState);
  dataPopUp$ = computed(() => this.statePopUp().dataPopUp$);
  loadedPopUp$ = computed(() => this.statePopUp().loadedPopUp$);
  loadingPopUp$ = computed(() => this.statePopUp().loadingPopUp$);
  errorPopUp$ = computed(() => this.statePopUp().errorPopUp$);

  constructor(
    @Inject(PLATFORM_ID) private platformId: object,
    private bannerService: BannerService,
    private transferStateService: TransferStateService
  ) {}

  private getBannerState(id: string) {
    let localData;
    try {
      localData = JSON.parse(localStorage.getItem('dateBanner') || '{}');
    } catch (error) {
      console.error('Error parsing local storage data:', error);
      localData = {};
    }
    const bannerState = localData[id] || {
      index: 0,
      lastClose: null,
      frequent: 0
    };
    const currentDate = new Date().toDateString();
    if (bannerState.lastClose) {
      const lastCloseDate = new Date(bannerState.lastClose).toDateString();
      if (currentDate !== lastCloseDate) {
        bannerState.frequent = 0;
      }
    }
    return bannerState;
  }

  private saveBannerState(
    id: string,
    index: number,
    lastClose: string,
    frequent: number
  ) {
    let localData;
    try {
      localData = JSON.parse(localStorage.getItem('dateBanner') || '{}');
    } catch (error) {
      console.error('Error parsing local storage data:', error);
      localData = {};
    }
    localData[id] = { index, lastClose, frequent };
    localStorage.setItem('dateBanner', JSON.stringify(localData));
  }

  get(variable: ListBannerConfigurationInputDto): Promise<true> {
    const existingDataBannerSlider =
      this.transferStateService.get<ResponseListBannerConfigurationDto>(
        STATE_BANNER_SLIDER_KEY,
        {} as ResponseListBannerConfigurationDto
      );
    const existingDataBannerPopup =
      this.transferStateService.get<ResponseListBannerConfigurationDto>(
        STATE_BANNER_POPUP_KEY,
        {} as ResponseListBannerConfigurationDto
      );
    return new Promise((resolve, reject) => {
      if (
        TypeListBanner.SLIDER === variable.listBannerConfigurationInput.type
      ) {
        this.stateSlider.set({
          dataSlider$: existingDataBannerSlider,
          loadedSlider$: false,
          loadingSlider$: true,
          errorSlider$: null
        });
      } else if (
        TypeListBanner.POPUP === variable.listBannerConfigurationInput.type
      ) {
        this.statePopUp.set({
          dataPopUp$: existingDataBannerPopup,
          loadedPopUp$: false,
          loadingPopUp$: true,
          errorPopUp$: null
        });
      }

      this.bannerService
        .listBannerConfiguration(variable)
        .pipe(takeUntil(this.terminate$))
        .subscribe({
          next: ({ data }) => {
            if (
              TypeListBanner.SLIDER ===
              variable.listBannerConfigurationInput.type
            ) {
              this.transferStateService.set(STATE_BANNER_SLIDER_KEY, data);
              this.stateSlider.set({
                dataSlider$: data,
                loadedSlider$: true,
                loadingSlider$: false,
                errorSlider$: null
              });
            } else if (
              TypeListBanner.POPUP ===
              variable.listBannerConfigurationInput.type
            ) {
              this.transferStateService.set(STATE_BANNER_POPUP_KEY, data);
              this.statePopUp.set({
                dataPopUp$: data,
                loadedPopUp$: true,
                loadingPopUp$: false,
                errorPopUp$: null
              });
            }
            resolve(true);
          },
          error: (error) => {
            if (
              TypeListBanner.SLIDER ===
              variable.listBannerConfigurationInput.type
            ) {
              this.stateSlider.set({
                dataSlider$: {} as ResponseListBannerConfigurationDto,
                loadedSlider$: true,
                loadingSlider$: false,
                errorSlider$: error
              });
            } else if (
              TypeListBanner.POPUP ===
              variable.listBannerConfigurationInput.type
            ) {
              this.statePopUp.set({
                dataPopUp$: {} as ResponseListBannerConfigurationDto,
                loadedPopUp$: true,
                loadingPopUp$: false,
                errorPopUp$: error
              });
            }
            reject(error);
          }
        });
    });
  }

  // closeBanner(id: string) {
  //   // const currentTime = new Date().toISOString();
  //   // const bannerState = this.getBannerState(id);
  //   // this.saveBannerState(id, bannerState.index, currentTime, bannerState.frequent);
  // }

  shouldDisplayBanner(id: string, displayCycle: number): boolean {
    const bannerState = this.getBannerState(id);
    if (!bannerState.lastClose) {
      return true;
    }
    const currentTime = new Date().getTime();
    const lastClosedTime = new Date(bannerState.lastClose).getTime();
    const timeDiff = (currentTime - lastClosedTime) / 1000;
    const displayFrequent =
      this.statePopUp().dataPopUp$.listBannerConfiguration.bannerConfigurations.find(
        (c) => c.id === id
      )?.displayFrequent || 0;

    return timeDiff > displayCycle && bannerState.frequent < displayFrequent;
  }

  bannerPopupShow = computed(() => {
    const configurations =
      this.statePopUp().dataPopUp$.listBannerConfiguration.bannerConfigurations;

    if (!configurations || configurations.length === 0) {
      return null;
    }

    for (let i = 0; i < configurations.length; i++) {
      const config = configurations[i];
      const id = config.id;
      const displayCycle = config.displayCycle || 30;
      const bannerState = this.getBannerState(id);

      if (this.shouldDisplayBanner(id, displayCycle)) {
        const banner = config.banners[bannerState.index];
        const newIndex = (bannerState.index + 1) % config.banners.length;
        const newFrequent =
          newIndex === 0 ? bannerState.frequent + 1 : bannerState.frequent;

        if (
          i === configurations[i].displayFrequent ||
          configurations.slice(0, i).every((prevConfig) => {
            const prevState = this.getBannerState(prevConfig.id);
            return prevState.frequent === prevConfig.displayFrequent;
          })
        ) {
          const backendFrequent = config.displayFrequent;
          if (bannerState.frequent !== backendFrequent) {
            this.saveBannerState(
              id,
              newIndex,
              new Date().toISOString(),
              newFrequent
            );
            return banner;
          }
          return null;
        }

        const sameFrequentConfigs = configurations.filter(
          (conf) =>
            conf.displayFrequent === i &&
            this.shouldDisplayBanner(conf.id, conf.displayCycle)
        );
        if (sameFrequentConfigs.length > 0) {
          const nextBannerState = this.getBannerState(
            sameFrequentConfigs[0].id
          );
          const nextBanner =
            sameFrequentConfigs[0].banners[nextBannerState.index];
          return nextBanner;
        } else {
          return null;
        }
      }
    }

    return null;
  });

  bannerSliderDisplayCycle = computed(() => {
    const configurations =
      this.stateSlider().dataSlider$.listBannerConfiguration
        .bannerConfigurations;
    let displayCycle: number | undefined;
    configurations.forEach((item) => {
      if (item) {
        displayCycle = item.displayCycle;
      }
    });
    return displayCycle;
  });

  destroy() {
    this.terminate$.next();
    this.terminate$.complete();
  }
}
