import { Component, EventEmitter, Input, Output } from "@angular/core";
import { ModalService } from "../../../service/modal/modal.service";
import { CategoryModalComponent } from "../../../../main/home/category-modal/category-modal.component";
import { Warehouse} from "@youtap-web-lib";
import { GaService } from "../../../service/ga.service";

@Component({
  selector: "supplier-section",
  templateUrl: "./supplier-section.component.html"
})
export class SupplierSectionComponent {
  @Input() warehouses!: Warehouse;
  @Input() isFirstLoad = false;
  @Output() haveData: EventEmitter<boolean> = new EventEmitter<boolean>();
  page = 1;

  constructor(
    private modalService: ModalService,
    private gaService: GaService
  ) {
  }

  showCategory(warehouse: Warehouse) {
    const modalRef = this.modalService.open(CategoryModalComponent, {
      title: "Kategori",
      closeButton: true,
      data: {
        supplierId: warehouse.supplier.id
      }
    });
    modalRef.onResult().subscribe(
      (data) => {
        this.goToDetailSupplier(warehouse.supplier.slug, data.id);
      },
      (dismissed) => {
        dismissed;
      }
    );
  }

  checkLength(length: number) {
    if (!length) {
      this.haveData.emit(false);
    }
  }

  goToDetailSupplier(slug: string, category?: string, tierName?: string, name?: string) {
    if(tierName === 'official_partner') {
      this.gaService.addEvent('detail ' + name, 'YOS', "");
    }
  }
}
